
.z-switch[data-v-10] {
  display: inline-block;
  padding: 2px;
  background-color: #ccc;
  cursor: pointer;
  position: relative;
}
.z-switch-button[data-v-10] {
  display: inline-block;
  border-radius: 100%;
  background-color: #fff;
  position: absolute;
  left: 2px;
}
.z-switch-button-open[data-v-10] {
  left: auto;
  right: 2px;
}



