
html,body,h1,h2,ul {
  margin: 0;
  padding: 0;
}
.section {
  padding-top: 60px
}
body {
  font-size: 0;
}
#app {
  color: rgb(53, 73, 94);
  font-family: Source Sans Pro, Helvetica, sans-serif;
  font-size: 16px;
}
code {
  border-radius: 2px;
  font-family: Menlo,Monaco,Consolas,Courier,monospace;
  font-size: 0.9em;
  -webkit-font-smoothing: initial;
  -moz-osx-font-smoothing: initial;
}
code.z-code {
  color: #e96900;
  padding: 3px 5px;
  margin: 0 2px;
  background-color: #f8f8f8;
}
.heart {
  color: red;
}
a {
  color: rgb(65, 184, 131);
  text-decoration: none;
}
.header {
  text-align: center;
}
.content {
  padding: 0 10px 50px;
}
@media screen and (max-width: 768px) {
#app {
    font-size: 14px;
}
.z-navbar>ul {
    padding: 10px;
}
}
@media screen and (min-width: 768px) {
.main {
    text-align: center;
}
.content {
    display: inline-block;
    text-align: left;
}
.z-navbar>ul {
    padding: 10px 60px;
}
}
.footer {
  text-align: center;
  color: #fff;
  padding: 50px 0;
  background-color: #727f80;
}
.footer a {
  color: #fff;
}
.logo {
  width: 100px;
  height: 100px
}
.section {
  margin: 20px 0 0;
}
.introduce {
  margin: 10px 5px 0 0;
  display: inline-block;
}
.introduce:last-child {
  margin-right: 0;
}
.introduce-block {
  margin: 10px 0 0;
}
.z-navbar>ul {
  height: 40px;
  line-height: 40px;
}
.z-navbar>ul>li {
  list-style: none;
  float: right;
  margin: 0 0.6em;
}
.z-navbar>ul>li>a {
  color: #34495e;
  padding-bottom: 3px;
}
.z-navbar>ul>li>a:hover {
  border-bottom: 3px solid rgb(65, 184, 131);
}
.z-navbar .z-guide {
  float: left;
  position: relative;
}



